import React from "react";

function Form(props) {
  return (
    <form className="poll-form">
      <input type="email" className="input poll-form__input" placeholder=" Email" />
      <button className="poll-form__btn btn btn--green" type="button"></button>
      <label htmlFor="poll-form" className="circle-checkbox poll-form__checkbox">
        <input type="checkbox" id="poll-form" className="circle-checkbox__input" />
        <span className="circle-checkbox__icon"></span>
        <span className="circle-checkbox__label">  ""      ,       </span>
        <picture>
          <source media="(min-width: )" srcset="" />
          <img src="" alt="" />
        </picture>
      </label>
    </form>
  )
}

export default Form;
